@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,400;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inconsolata&display=swap);
html {
  background-color: #222;
}

body {
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #222;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

input, select, textarea, button{font-family:inherit;}

*:focus {
  outline: none;
}

a {
  color: inherit;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-family: 'Open Sans', sans-serif;
}

.light_root__3DraG {
    background-color: #eee;
    border-radius: 9999px;
    padding-left: 16px;
    padding-right: 16px;
}

.light_root__3DraG input {
    background-color: #eee;
    border: none;
    border-radius: 9999px;
}

.light_root__3DraG ::-webkit-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    opacity: 0.8; /* Firefox */
}

.light_root__3DraG :-ms-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    opacity: 0.8; /* Firefox */
}

.light_root__3DraG ::-ms-input-placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    opacity: 0.8; /* Firefox */
}

.light_root__3DraG ::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    opacity: 0.8; /* Firefox */
}

