.root {
    background-color: #eee;
    border-radius: 9999px;
    padding-left: 16px;
    padding-right: 16px;
}

.root input {
    background-color: #eee;
    border: none;
    border-radius: 9999px;
}

.root ::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    opacity: 0.8; /* Firefox */
}
